$columns: 12;
$offset: 2.4%;
$offset_one_side: ($offset / 2);
$atom: (100% / $columns);

$break_lg: 1200px;
$break_md: 992px;
$break_sm: 720px;
$break_xs: 576px;

@mixin reset() {
    body,div,dl,dt,dd,ul,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,input,textarea,p,blockquote,th,td {
        margin: 0;
        padding: 0; }

    table {
        border-collapse: collapse;
        border-spacing: 0; }

    fieldset,img,abbr {
        border: 0; }

    address,caption,cite,code,dfn,em,strong,th,var {
        font-style: normal;
        font-weight: normal; }

    ul li {
        list-style: none; }

    caption,th {
        text-align: left; }

    h1,h2,h3,h4,h5,h6 {
        font-size: 100%;
        font-weight: normal; }

    sup {
        vertical-align: text-top; }

    sub {
        vertical-align: text-bottom; }

    input,textarea,select {
        font-family: inherit;
        font-size: inherit;
        font-weight: inherit; }

    legend {
        color: #000; }

    article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section,main {
        display: block; }

    img {
        max-width: 100%;
        height: auto; } }



@mixin wrapper() {
    max-width: 1250px;
    padding-left: 3.2%;
    padding-right: 3.2%;
    margin: 0 auto;

    @media screen and (max-width: $break_lg) {
        padding-left: 30px;
        padding-right: 30px; }


    @media screen and (max-width: $break_md) {
        padding-left: 15px;
        padding-right: 15px; }


    @media screen and (max-width: $break_sm) {
        padding-left: 15px;
        padding-right: 15px; }


    @media screen and (max-width: $break_xs) {
        padding-left: 15px;
        padding-right: 15px; } }


@mixin row-flex() {
    display: flex;
    flex-wrap: wrap;
    margin-left: ($offset_one_side * -1);
    margin-right: ($offset_one_side * -1); }

@mixin row-float() {
    margin-left: ($offset_one_side * -1);
    margin-right: ($offset_one_side * -1);
    @include clearfix(); }

@mixin col() {
    box-sizing: border-box;
    margin-left: $offset_one_side;
    margin-right: $offset_one_side;
    word-wrap: break-word; }

@mixin col-float() {
    float: left; }

@mixin col-padding() {
    padding-left: $offset_one_side;
    padding-right: $offset_one_side;
    word-wrap: break-word; }

@mixin size($n) {
    $value: $atom * $n;
    width: $value - $offset; }

@mixin offset($n) {
    $value: $atom * $n;
    margin-left: $value + $offset_one_side;
    margin-right: $value + $offset_one_side; }

@mixin offset-left($n) {
    $value: $atom * $n;
    margin-left: $value + $offset_one_side; }

@mixin offset-right($n) {
    $value: $atom * $n;
    margin-right: $value + $offset_one_side; }

@mixin offset-padding($n) {
    $value: $atom * $n;
    padding-left: $value + $offset_one_side;
    padding-right: $value + $offset_one_side; }

@mixin offset-left-padding($n) {
