.container {
  max-width: 1250px;
  padding-left: 3.2%;
  padding-right: 3.2%;
  margin: 0 auto;
}

.header {
  padding: 51px 0 142px;
  text-align: center;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.2%;
  margin-right: -1.2%;
  align-items: center;
  justify-content: center;
}

.header__nav {
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
}

.header__logo {
  margin-right: auto;
}

.menutoggle {
  display: none;
}

#menuCheck {
  display: none;
}

.header__menu {
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
}

li:nth-last-child(-n+3) {
  padding-left: 0;
}

li {
  padding-left: 50px;
}

.nav__icons {
  margin-left: 20px;
}

.nav__icons-icon {
  padding-left: 20px;
}

.header__text {
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  width: 80.93333%;
  margin-top: 167px;
  margin-bottom: 34px;
}

.header__text-btns {
  padding-top: 70px;
}

.header__btn {
  padding: 23px 52px;
}

.btn-o {
  box-sizing: border-box;
}

.section__text {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  width: 30.93333%;
  margin-left: 9.53333%;
}

.section__text-title {
  padding-top: 106px;
}

.section__text-decor {
  padding-top: 25px;
}

.section__text-descr {
  padding: 32px 0 52px;
}

.section__text-img {
  padding-bottom: 114px;
}

.section__img {
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  width: 47.6%;
  padding-top: 55px;
}

.ingredients .row {
  justify-content: flex-end;
}

.section__text-bl {
  margin: 130px 0 101px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  width: 47.6%;
  align-self: flex-end;
  border: 1px solid #fbf7c0;
  background-color: #000000;
  opacity: 0.67;
  text-align: center;
}

.section__text-title_i {
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  width: 64.26667%;
  padding-top: 83px;
}

.section__text-descr_i {
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  width: 80.93333%;
  margin: 30px 0 70px;
}

.section__text-img {
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  width: 80.93333%;
  justify-content: space-around;
}

.menu {
  padding: 52px 0 100px;
}

.menu .row {
  align-items: flex-start;
}

.menu__item {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  width: 30.93333%;
  margin-left: 9.53333%;
  margin-right: 9.53333%;
}

.menu__item-title {
  margin: 16px;
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  width: 97.6%;
  text-align: center;
}

.menu__item-decor {
  max-height: 28px;
}

.menu__item-decor img {
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
}

.menu__item-list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.list-items {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 37px;
}

.item-title {
  margin-right: auto;
}

.item-price {
  margin-left: auto;
}

.hr {
  width: 100%;
  height: 1px;
  border: 1px solid #fbf7c0;
  background-color: #c89f2d;
  margin: 5px 0 5px;
}

.reviews {
  padding: 100px 0 101px;
}

.reviews__text {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid #fbf7c0;
  background-color: #000000;
  padding: 82px 74px 103px;
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  width: 64.26667%;
}

.reviews__text-title {
  margin-bottom: 23px;
}

.reviews__text-decor {
  margin-bottom: 20px;
}

.reviews__text-descr-au {
  margin-top: 50px;
}

.reserv {
  padding: 100px 0 103px;
}

.reserv .row {
  justify-content: flex-start;
  align-items: stretch;
}

.reserv__img {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  width: 47.6%;
}

.reserv__text {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  width: 30.93333%;
  margin-left: 9.53333%;
  margin-right: 9.53333%;
}

.reserv__text-title {
  margin-bottom: 32px;
}

.reserv__text-decor {
  margin-bottom: 32px;
}

.reserv__text-descr {
  margin-bottom: 60px;
}

.reserv__text-form {
  display: flex;
  align-content: stretch;
  flex-wrap: wrap;
  justify-content: space-between;
}

footer {
  padding: 61px 0 72px;
}

footer .row {
  align-items: stretch;
}

.footer__text {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  margin-left: 1.2%;
  margin-right: 1.2%;
  word-wrap: break-word;
  width: 30.93333%;
}

.footer__text-title {
  margin-bottom: 21px;
}

.footer__text-img {
