}

.footer__text-icons {
  margin-top: auto;
}

@media screen and (max-width: 1200px) {
  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 992px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .section__text-bl {
    width: 55.93333%;
  }
}

@media screen and (max-width: 720px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .header__nav {
    align-items: stretch;
    position: relative;
  }

  .menutoggle {
    display: inline-block;
    font-size: 30px;
    color: #fff;
    margin-left: auto;
    width: 5.93333%;
  }

  #menuCheck:checked + .header__menu {
    display: flex;
    position: absolute;
    left: 60%;
    top: 90%;
    right: 5%;
    flex-wrap: wrap;
    flex-direction: column;
    background-color: rgba(245, 170, 32, 0.4);
    width: 47.6%;
  }

  .header__menu {
    display: none;
  }

  li:nth-last-child(-n+3) {
    display: none;
  }

  .section__text {
    width: 64.26667%;
  }

  .section__img {
    width: 64.26667%;
  }

  .section__img {
    padding-top: 0;
  }

  .section__text-bl {
    width: 80.93333%;
  }

  .menu__item {
    width: 64.26667%;
  }
}

@media screen and (max-width: 576px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}